import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/core/services/auth/auth.service';
import { UserData } from 'src/app/modules/auth/models/userData.model';
import { ProfileService } from '../../services/profile.service';

@Component({
  selector: 'app-my-orders',
  templateUrl: './my-orders.component.html',
  styleUrls: ['./my-orders.component.scss']
})
export class MyOrdersComponent implements OnInit {
  userData: UserData;
  requestData=[];
  constructor(
    private authSrvc: AuthService,
    private _profileServ: ProfileService
    ) { }

  ngOnInit(): void {
    this.authSrvc.userData$.subscribe((data) => {
      if (data) this.userData = data;
    });
    this.getAllTracks()
  }

  getAllTracks() {
    this._profileServ.getAllTrackOrder(this.userData.id)
    .subscribe((res) => {
      console.log('resssss', res);
      this.requestData = res.data
      // this.trackData = res.data
      // console.log('here', this.trackData);
      // if(this.trackData){}
      // this.trackData.map((order) => {
      //   this.stepper.selected.completed = true;
      //   for(let i = 1; i < order.newStatus; i++) {

      //     this.stepper.next();
      //   }
      // })
    })
  }

}
